import c from './navigation.module.scss';
import { NavLink } from "react-router-dom";
import { isLinkActiveHelper } from '../../../static/helpers/isLinkActiveHelper';

type Props = {
    closeBurger?: () => void
}

export const NavLinks: React.FC<Props> = ({closeBurger}) => {
    return <div>
        <NavLink isActive={(match, location) => isLinkActiveHelper(match, location, true, '/gastos/foodPrograms', '', '', true)} onClick={closeBurger} activeClassName={c.active} className={c.link} to='/gastos/foodPrograms'> </NavLink>
        <NavLink onClick={closeBurger} activeClassName={c.active} className={c.link} to='/gastos/buisnesLanch'>-</NavLink>
        <NavLink onClick={closeBurger} activeClassName={c.active} className={c.link} to='/gastos/gastroShop'>Gastro Shop</NavLink>
        <NavLink onClick={closeBurger} activeClassName={c.active} className={c.link} to='/gastos/aboutUs'> </NavLink>
        <NavLink onClick={closeBurger} activeClassName={c.active} className={c.link} to='/gastos/blog'></NavLink>
    </div>
}