import { Button } from "@mui/material";
import c from "./doOrder.module.scss";
import { TextField } from "./TextField";
import { useModalStatusChanger } from "../../../../static/helpers/useModalStatusChanger";

export const DoOrder: React.FC = () => {
    const changeModalStatus = useModalStatusChanger();

    return <div className={c.mainContainer}>
        <div className={c.doOrderContainer}>
            <h2 className={c.doOrder}> </h2>
            <p>           </p>
        </div>
        <div className={c.textfieldsContainer}>
            <TextField id='doOrderName' helperText='' type='name' />
            <TextField id='doOrderPhone' helperText='' type='phone' />
        </div>
        <div className={c.buttonsContainer}>
            <Button onClick={() => changeModalStatus('phoneModal', true)} className={c.button} variant='outlined'>  </Button>
            <p className={c.or}></p>
            <Button onClick={() => changeModalStatus('onlineModal', true)} className={c.button} variant='outlined'> </Button>
        </div>
    </div>
};